<template>
  <div class="contact">
    <v-container fluid>
      <v-row justify="center" class="mt-5">
        <v-col cols="12" class="text-h5 text-center" id="form_top"></v-col>
        <v-col cols="12" md="8" class="mb-6">
          <v-alert
            :type='alert_type'
            dismissible
            v-model="alert"
            dense
          >
          {{ alert_message }}
          </v-alert>
          <v-form ref="form">
            <v-row>
              <v-col cols="5" class="text-body-1 text-center cell_top remove_border-right">
                <br>
                <span style="color: red; font-size: 16px;"></span>
              </v-col>
              <v-col cols="7" class="cell_top">
                <v-text-field
                  v-model="name"
                  :rules="nameRules"
                  required
                  outlined
                  dense
                ></v-text-field>
              </v-col>
              <v-col cols="5" class="text-body-1 text-center cell remove_border-right">
                E-mail<br>
                <span style="color: red; font-size: 16px;"></span>
              </v-col>
              <v-col cols="7" class="cell">
                <v-text-field
                  v-model="email"
                  :rules="emailRules"
                  required
                  outlined
                  dense
                  type="email"
                ></v-text-field>
              </v-col>
              <v-col cols="5" class="text-body-1 text-center cell remove_border-right">
                <br>
                <span style="color: red; font-size: 16px;">()</span>
              </v-col>
              <v-col cols="7" class="cell">
                <v-text-field
                  v-model="phone"
                  :rules="phoneRules"
                  required
                  outlined
                  dense
                  type="tel"
                ></v-text-field>
              </v-col>
              <v-col cols="5" class="text-body-1 text-center cell remove_border-right">
                <br>
                <span style="color: red; font-size: 16px;"></span>
              </v-col>
              <v-col cols="7" class="cell">
                <v-select
                  outlined
                  v-model="category"
                  :items="items"
                  :rules="categoryRules"
                  dense
                  required
                ></v-select>
              </v-col>
              <v-col cols="5" class="text-body-1 text-center cell remove_border-right">
                
              </v-col>
              <v-col cols="7" class="cell">
                <v-text-field
                  v-model="title"
                  outlined
                  dense
                ></v-text-field>
              </v-col>
              <v-col cols="5" class="text-body-1 text-center cell remove_border-right">
                <br>
                <span style="color: red; font-size: 16px;"></span>
              </v-col>
              <v-col cols="7" class="cell">
                <v-textarea
                  v-model="content"
                  :counter="500"
                  :rules="contentRules"
                  required
                  outlined
                  auto-grow
                ></v-textarea>
              </v-col>
            </v-row>
            <v-row justify="center" class="mt-6">
              <v-btn
                color="success"
                large
                @click="submit"
                :loading="loading"
              ></v-btn>
            </v-row>
          </v-form>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from "axios"
export default {
  data() {
    return {
      name: '',
      email: '',
      phone: null,
      category: '',
      items: ['', '', ''],
      title: '',
      content: '',
      nameRules: [
        v => !!v || '',
      ],
      emailRules: [
        v => !!v || '',
        v => /.+@.+\..+/.test(v) || '',
      ],
      phoneRules: [
        v => !!v || '',
        v => /^[0-9]+$/.test(v) || ''
      ],
      categoryRules: [
        v => !!v || '',
      ],
      contentRules: [
        v => !!v || '',
        v => v.length <= 500 || '500',
      ],
      loading: false, //
      //
      alert: false,
      alert_type: 'success',
      alert_message: '',
    }
  },
  methods: {
    submit() {
      //
      if(this.$refs.form.validate()){
          this.loading = true
          try{
            //
            axios.post('./api/contact.php', {
              name: this.name,
              email: this.email,
              phone: this.phone,
              category: this.category,
              title: this.title,
              content: this.content
            })
            .then((response) => {
              if(response.data.result){
                console.log(': ')
                this.alert_type = 'success'
                this.alert_message = '!'
                this.alert = true
                this.save_to_db()
                this.$refs.form.reset()
              } else {
                console.log(': ')
                this.alert_type = 'error'
                this.alert_message = ''
                this.alert = true  
              }
                this.$vuetify.goTo('#form_top')
                this.loading = false
            })
          }
          catch{
            console.log('DB: ')
            this.loading = false
            this.alert_type = 'error'
            this.alert_message = ''
            this.alert = true
            this.$vuetify.goTo('#form_top')

          }
      //
      } else {
          this.alert_type = 'warning'
          this.alert_message = ''
          this.alert = true
          this.$vuetify.goTo('#form_top')

      }
    },
    save_to_db() {
      try{
        //
        axios.post('./api/saveMail.php', {
          name: this.name,
          email: this.email,
          phone: this.phone,
          category: this.category,
          title: this.title,
          content: this.content
        })
        .then((response) => {
          if(response.data.result) {
            console.log('DB: ')
          } else {
            console.log('DB: ')
          }
        })
      }
      catch{
        console.log('DB: ')
      }
    },
  },
}
</script>

<style scoped>
.cell {
  border: solid;
  border-top: none;
}

.cell_top {
  border: solid;
}

.remove_border-right {
  border-right: none;
}
</style>